.bingo-board {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 5px;
    background-color: black;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    margin: auto;
    margin-bottom: 50px;
    border: 10px solid black;
}

.bingo-board__space {
    display: -ms-inline-flexbox;
    display: inline-flex;
    background-color: white;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 150px;
    width: 150px;
    padding: 10px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.bingo-board__space:hover {
    background-color: lightblue;
}

.bingo-board__space--selected, .bingo-board__space--selected:hover {
    background-color: blue;
    color: white;
}

.bingo-board__victory {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1000;
    pointer-events: none;
}


